<template>
  <div
    style="padding-bottom: 50px;"
    @focusin.capture="handlePageFocusIn"
    @focusout.capture="handlePageFocusOut"
  >
    <el-form
      ref="form"
      :model="orderParams"
      :rules="rules"
    >
      <div class="confirm"><img src="./icon2.png"  style="width: 12px;margin-right: 5px" alt="">    温馨提示：权益套餐一经激活，不能退订。</div>
      <div class="content">
        <div class="confirm-top">
          <img :src="otherOrderParams.pic" class="confirm-top-img" alt/>
          <div class="confirm-top-content">
            <div class="title">{{orderParams.product_name}}</div>
            <div class="operation-box">
              <span class="order-price">￥{{orderParams.price}}</span>
              <div class="order-operation2">
                <img src="~@/assets/img/orderConfirm/minus.png" alt="" @click="numReduce">
                <el-form-item class="line-block" prop="num">
                  <el-input-number
                    :min="1" :max="1000000" :controls="false" :precision="0"
                    v-model="orderParams.num"
                    @change="handleGoodsNumChange"
                  />
                </el-form-item>
                <img src="~@/assets/img/orderConfirm/plus.png" alt="" @click="numAdd">
              </div>
            </div>
          </div>
        </div>
        <div class="set-meal">
          <div class="set-meal-title">单次</div>
          <div class="set-meal-content">套餐包含权益</div>
        </div>
        <!-- 所有权益 -->
        <div class="commodity-list" v-for="(item,index) in otherOrderParams.boonList" :key="index">
          <p class="title">
            <img class="icon" src="./icon1.png" alt="">
            {{item.title}}
          </p>
          <div class="commodity-item-wrapper">
            <div class="commodity-item">
              <div>使用次数：</div>
              <div>
                {{item.useNum}}
              </div>
            </div>
            <div class="commodity-item">
              <div>覆盖范围：</div>
              <div>
                {{item.area}}
              </div>
            </div>
          </div>
        </div>
        <div class="underline1"></div>
        <div class="set-meal purchaser">
          <div class="set-meal-content">购买人信息</div>
        </div>
        <div class="purchaser-box">
          <div>
            <span>姓名</span>
            <span>(必填)</span>
          </div>
          <el-form-item prop="customer_name">
            <el-input v-model="orderParams.customer_name" maxlength="20" placeholder="请输入姓名"/>
          </el-form-item>
        </div>
        <div class="purchaser-box">
          <div>
            <span>性别</span>
            <span>(必填)</span>
          </div>
          <el-form-item prop="sex">
            <el-radio v-model="orderParams.sex" :label="1">先生</el-radio>
            <el-radio v-model="orderParams.sex" :label="0">女士</el-radio>
          </el-form-item>
        </div>
        <div class="purchaser-box">
          <div>
            <span>手机号</span>
            <span>(必填)</span>
          </div>
          <el-form-item prop="phone">
            <el-input maxlength="11" placeholder="请输入手机号" :value="orderParams.phone" @input="handlePhoneInput"/>
          </el-form-item>
        </div>
        <div class="underline2"></div>
        <div class="price-box">
          <div>商品总价</div>
          <div>
            ￥{{totalPrice}}
          </div>
        </div>
        <div class="price-box" v-if="couponValue">
          <div>优惠券</div>
          <div>-¥100</div>
        </div>
      </div>
      <div class="line"></div>
      <div class="footer">
        <div>
          实付款
          <span style="font-weight: 600;">￥{{totalMoney}}</span>
        </div>
        <div @click="submitOrder">提交订单</div>
      </div>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        isSubmitting: null,
        couponValue: 0, // 优惠券的面额（等于0就是没有优惠券）
        orderParams: {
          openid: this.$wx.getOpenId(),
          customer_name: "",
          product_name: "",
          phone: "",
          sex: 1,
          num: 1,
          total_money: "",
          totalPrice: "",
          price: 0,
          useCoupon: 0, // 0不使用，1使用
        },
        otherOrderParams: {},
        rules: {
          num: {
            trigger: 'blur',
            required: true,
            message: '请填写数量',
          },
          sex: {
            trigger: 'blur',
            required: true,
            message: '请选择性别',
          },
          customer_name: {
            trigger: 'blur',
            required: true,
            message: '请填写姓名',
          },
          phone: {
            trigger: 'blur',
            required: true,
            pattern: /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/,
            message: '请填写正确的手机号码',
          },
        },
        scrollTop: 0,
      };
    },
    computed: {
      totalPrice() {
        this.orderParams.totalPrice = (this.orderParams.price * parseInt(this.orderParams.num)).toFixed(2);
        return this.orderParams.totalPrice;
      },
      totalMoney() {
        this.orderParams.total_money = this.orderParams.totalPrice - this.couponValue;
        return this.orderParams.total_money;
      }
    },
    methods: {
      handlePageFocusIn() {
        this.scrollTop = document.documentElement.scrollTop;
      },
      handlePageFocusOut() {
        document.documentElement.scrollTop = this.scrollTop;
      },
      handlePhoneInput(val) {
        this.orderParams.phone = val.replace(/[^0-9]/, '');
      },
      handleGoodsNumChange(currentValue, oldValue) {
        if (!currentValue) {
          this.$nextTick(() => {
            this.orderParams.num = oldValue;
          });
        }
      },
      numAdd() {
        this.orderParams.num++;
      },
      numReduce() {
        if (this.orderParams.num > 0) {
          this.orderParams.num--;
        }
      },
      loadCouponInfo() {
        this.http.get(this.api.selectCoupon, {
          openid: this.orderParams.openid,
        }, true).then(res => {
          if (res.status !== 200) {
            return;
          }

          if (res.data.showCoupon === 0) {
            this.couponValue = 100;
            this.orderParams.useCoupon = 1;
          }
        });
      },
      loadGoodsInfo() {
        let orderParams = null, otherOrderParams = null;
        // 目前有3个页面
        switch (this.$route.params.payType) {
          case 'leisureTime':
            orderParams = {
              product_name: '我爱休闲时光',
              price: 1299,
            };
            otherOrderParams = {
              pic: require("./cover-leisureTime.png"),
              // 权益、福利。。
              boonList: [
                {
                  title: '全国特色度假酒店/民宿',
                  useNum: '不限次数',
                  area: '15个省60个城市211家酒店',
                  cost: '￥6000',
                },
              ],
            };
            break;
          case 'hotel':
            orderParams = {
              product_name: '精奢酒店优选礼包',
              price: 2180,
            };
            otherOrderParams = {
              pic: require("./cover-hotel.png"),
              // 权益、福利。。
              boonList: [
                {
                  title: '臻选高端酒店全免自助餐，双人（国内）',
                  useNum: '2次',
                  area: '45个城市120家酒店',
                  cost: '￥1300',
                },
                {
                  title: '臻选高端酒店2人同行1人免单（国内）',
                  useNum: '每天1次（特殊节假日除外）',
                  area: '45个城市120家酒店',
                  cost: '￥6000',
                },
                {
                  title: '臻选高端酒店全免住宿（境外）',
                  useNum: '1间夜（部分指定城市3天2晚）',
                  area: '33个城市86家酒店',
                  cost: '￥2000',
                },
              ],
            };
            break;
          case 'life':
            orderParams = {
              product_name: '小资生活',
              price: 699,
            };
            otherOrderParams = {
              pic: require("./cover-life.png"),
              // 权益、福利。。
              boonList: [
                {
                  title: '咖啡折扣券权益',
                  useNum: '每天2杯',
                  area: 'Costa/星巴克/Coco',
                  cost: '￥2000',
                },
                {
                  title: '贵州、桂林、昆明/大理/丽江（三选一）双人',
                  useNum: '1次',
                  area: '6天5晚，即当地跟团游',
                  cost: '￥3000',
                },
              ],
            };
            break;
          default:
            alert('参数错误！');
        }

        if (orderParams.product_name) {
          document.title = orderParams.product_name;
        }

        Object.assign(this.orderParams, orderParams);
        Object.assign(this.otherOrderParams, otherOrderParams);
      },
      //提交订单
      submitOrder() {
        if (this.isSubmitting) {
          return;
        }
        this.$refs.form.validate(async (valid, errMsg) => {

          if (!valid) {
            for (let k in errMsg) {
              this.$toast.fail(errMsg[k][0].message);
              break;
            }
            return;
          }

          this.isSubmitting = true;
          let result = await this.http.post(this.api.makeOrder, this.orderParams, true);
          this.isSubmitting = false;

          if (result.status !== 200) {
            this.$toast.fail(result.message);
            return;
          }

          result = result.data;
          // result = {
          //   appId:
          //     "wx9db482df5987c784",
          //   nonceStr:
          //     "1zxgky8z87v24o35z8edvxw6zhzr9ffl",
          //   package:
          //     "prepay_id=wx281104187285986eb3ed49351152774900",
          //   paySign:
          //     "F76F5CEC1D89688653C1326496AB6CCB",
          //   signType:
          //     "MD5",
          //   timeStamp:
          //     "1574910258",
          // };

          WeixinJSBridge.invoke(
            'getBrandWCPayRequest',
            {
              "appId": result.appId,     //公众号名称，由商户传入
              "timeStamp": result.timeStamp,         //时间戳，自1970年以来的秒数
              "nonceStr": result.nonceStr, //随机串
              "package": result.package,
              "signType": result.signType,         //微信签名方式：
              "paySign": result.paySign,//微信签名
            },
            res => {
              if (res.err_msg == "get_brand_wcpay_request:ok") {
                // 使用以上方式判断前端返回,微信团队郑重提示：
                //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
                if(this.orderParams.product_name=='我爱休闲时光'){
                  this.$router.push(`/paymentSuccess?love=true`);
                }
                else{
                  this.$router.push(`/paymentSuccess`);
                }
              } else if (res.err_msg == "get_brand_wcpay_request:cancel") {
                // this.$toast.fail('已取消支付！');
                this.http.post(this.api.cancelOrder, {
                  openid: this.orderParams.openid,
                });
              } else {
                this.$toast.fail(res.err_msg);
              }
            });
        });
      }
    },
    created() {
      this.loadCouponInfo();
      this.loadGoodsInfo();
    },
  };
</script>

<style lang="scss" scoped>
  /deep/ {

    .el-form-item {
      margin-bottom: 0;

      .el-form-item__content {
        line-height: inherit;
      }

      .el-form-item__error {
        right: 0;
      }

      .el-input__inner {
        padding: 0;
      }
    }
  }

  .line-block {
    display: inline-block;
    vertical-align: middle;
  }

  .content {
    padding: 22px 0 15px 15px;
    background-color: #FFF;
  }

  .confirm-top {
    display: flex;
    padding-right: 15px;

    .confirm-top-img {
      width: 110px;
      height: 90px;
      border-radius: 5px;
    }

    .confirm-top-content {
      flex: 1;
      padding-left: 15px;

      .title {
        font-weight: 600;
        font-size: 20px;
      }

      .operation-box {
        display: flex;
        margin-top: 25px;

        .order-price {
          flex: 1;
          font-size: 17px;
          line-height: 1;
          margin-top: 10px;
          color: #fac86d;
          font-weight: 600;
        }

        .order-operation2 {
          text-align: right;

          img {
            vertical-align: middle;
            width: 16px;
            cursor: pointer;
          }

          /deep/ {
            .el-input-number {
              width: 80px;
              margin: 0 5px;
            }

            .el-input__inner {
              background-color: #f0f0f0;
              border-radius: 0;
              border: none;
              height: 30px;
              line-height: 30px;
              font-size: 18px;
              color: #000;
            }
          }
        }

        .order-operation {
          input {
            width: 80px;
            height: 30px;
            border: 1px solid #ddd;
            border-radius: 3px;
            padding: 0 8px;
            text-align: center;
            box-sizing: border-box;
          }

          span:last-child {
            font-size: 25px;
          }
        }
      }
    }
  }

  .set-meal {
    display: flex;
    margin-top: 20px;
    line-height: 21px;
    padding-right: 15px;

    .set-meal-title {
      box-sizing: border-box;
      color: #fac86d;
      border: 1px solid #fac86d;
      font-size: 12px;
      margin-right: 5px;
      border-radius: 5px;
      width: 35px;
      height: 20px;
      line-height: 20px;
      text-align: center;
    }

    .set-meal-content {
      font-size: 15px;
      font-weight: 600;
    }
  }

  .commodity-list {
    margin-top: 14px;

    .title {
      line-height: 24px;
      color: #000;
      font-weight: 600;
      font-size: 14px;

      .icon {
        height: 16px;
        vertical-align: -3px;
        margin-right: 1px;
      }
    }

    .commodity-item-wrapper {
      padding: 9px 20px;
      line-height: 23px;
      border-radius: 5px;

    }

    .commodity-item {
      display: flex;
      font-weight: 500 !important;
      color: #999999;
      font-size: 14px;
    }
  }

  .commodity-box {
    display: flex;
    font-size: 13px;
    opacity: 0.5;
    padding-right: 15px;

    div:first-child {
      width: 60px;
    }

    div:last-child {
      flex: 1;
      text-align: right;
    }
  }

  .purchaser {
    margin-top: 13px;
  }

  .purchaser-box {
    display: flex;
    font-size: 15px;
    margin-top: 17px;
    line-height: 21px;
    padding-right: 15px;
    color: #000;

    /deep/ .el-input input {
      line-height: 21px;
      height: 21px;
      border: none;
      text-align: right;
    }

    > div:first-child {
      width: 88px;

      span:first-child {
        opacity: 0.74;
      }

      span:last-child {
        font-size: 8px;
        color: #FF3B30;
      }
    }

    > div:last-child {
      flex: 1;
      text-align: right;
    }
  }

  .underline1 {
    margin-top: 19px;
    border-bottom: 1px solid #f0f0f0;
  }

  .underline2 {
    margin: 18px 0 15px 0;
    border-bottom: 1px solid #f0f0f0;
  }

  .price-box {
    display: flex;
    opacity: 0.49;
    font-size: 15px;
    margin-top: 14px;
    padding-right: 15px;

    div:first-child {
      width: 60px;
    }

    div:last-child {
      flex: 1;
      text-align: right;
    }
  }

  .footer {
    display: flex;
    position: fixed;
    z-index: 1;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #f0f0f0;
    font-size: 16px;
    line-height: 50px;
    height: 50px;
    background-color: #fff;
    text-align: center;

    div:first-child {
      flex: 1;
      font-size: 10px;
      color: #341A0F;
      font-weight: 600;
      span {
        color: #FAC86D;
        font-size: 21px;
      }
    }

    div:last-child {
      width: 150px;
      color: #fff;
      background-color: #341a0f;
      font-weight: 600;
    }

  }
  .confirm {
    height: 25px;
    line-height: 25px;
    background: #EDF7FC;
    padding-left: 15px;
    color:#FF3B30; ;
    display: flex;
    align-items: center;
    font-size: 11px;
  }

</style>



